import { Button } from "@mui/material";
import {useRouter} from "next/router";
import {useTranslation,nextPreload} from "@fanfanlo"
import { useEffect } from "react";
export function AddExternalMarketButton(){
    const router = useRouter()
    const {t} = useTranslation('homepage/components/markets/content');
    
      useEffect(() => {
        nextPreload(router, '/add-market');
      }, []);
    function onClick(){
        router.push("/add-market")
    }
    return (
        <Button onClick={onClick}>
            {t("AddExternalMarketButton.name")}
        </Button>
    )
}