
import { clear, createStore, del, entries, get, set } from 'idb-keyval';
import { getProxyObject, getProxyTarget, toProxy, watchUpdates } from "../watcher";
import { INamespaceOptions, IStoreWithData } from "./store-intf";
import { Log } from "../log/Log";
import _ from 'lodash';
